<template>
  <div>
    <div style="margin-top: 8px; margin-bottom:16px;display: flex">
      <el-input style="flex-grow:1" v-model="filename" placeholder="Input the filename..">
        <el-select slot="prepend" v-model="value" placeholder="Choose..">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select> 
      </el-input>
      <el-button style="width:120px;margin-left:8px;" type="success" @click="saveContent()">saveContent</el-button>
    </div>

    <quill-editor :content="content" :options="editorOption" @change="onEditorChange($event)"></quill-editor>

    <el-dialog title="result" :visible.sync="centerDialogVisible" width="30%" center>
      <div style="text-algin: center">
        <span>{{ result.fullpath }}</span>
        <img :src="result.imgurl" />
      </div>
    </el-dialog>
  </div>
</template>
<style>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}

.ql-editor {
  min-height: 400px;
}
</style>



<script>
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";

import { quillEditor } from "vue-quill-editor";
import fileService from "../api/file";
import { Loading } from "element-ui";

export default {
  components: {
    quillEditor
  },
  data() {
    return {
      options: [
        {
          value: "js",
          label: "JS"
        },
        {
          value: "css",
          label: "CSS"
        },
        {
          value: "html",
          label: "Html"
        }
      ],
      value: "html",

      actions: new Map([
        ["js", "saveAsJS"],
        ["css", "saveAsCss"],
        ["html", "saveAsHtml"]
      ]),
      isCollapse: true,
      result: {
        imgurl: "",
        fullpath: ""
      },
      centerDialogVisible: false,
      filename: "",
      content: "<p>example content</p>",
      editorOption: {
        /* quill options */
      }
    };
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    onEditorChange(event) {
      this.content = event.html;
      console.log(this.content);
    },
    saveAsJS() {
      return fileService.saveAsJS;
    },
    saveAsCss() {
      return fileService.saveAsJS;
    },
    saveAsHtml() {
      return fileService.saveAsHtml;
    },
    saveContent() {
      let exec = fileService;
      const func = this.actions.get(this.value);
      let save = exec[func];
      let loadingInstance1 = Loading.service({ fullscreen: true });
      save(this.content, this.filename)
        .then(res => {
          loadingInstance1.close();
          this.result.imgurl = res.data.dataurl;
          this.result.fullpath = res.data.result;

          this.centerDialogVisible = true;
        })
        .catch(err => {
          loadingInstance1.close();
          this.$message.error("Failed");
        });
    }
  },

  mounted() {}
};
</script>