import request from '../request/index' 
 
const getFiles = (data) =>{
    return request.post({
        url: 'file/getFiles',
        data: data
    })
}

const removeFileById = (id) =>{
    return request.post({
        url: 'file/removeFileById',
        data: {
            id
        }
    })
}

const saveAsHtml = (data ,filename ) =>{
    return request.post({
        url: 'file/saveAsHtml',
        data: {
            filename,
            data
        }
    })
}

const saveAsJS = (data ,filename) =>{
    return request.post({
        url: 'file/saveAsJS',
        data: {
            filename,
            data
        }
    })
}

const saveAsCss = (data ,filename) =>{
    return request.post({
        url: 'file/saveAsCss',
        data: {
            filename,
            data
        }
    })
}





export default {
    getFiles,
    removeFileById,
    saveAsHtml,
    saveAsJS,
    saveAsCss
}